  .containerpro {
    position: relative;
  }
  .bottomText {
    position: absolute;
    padding: 8%;
    bottom: 0%;
    right: 0%;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
  }
  
  .imagepro {
    display: block;
    width: 100%;
    height: 60vh!important;
    margin-top: 5%;
  }
  
  .overlaypro {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: #008CBA;
  }
  
  .containerpro:hover .overlaypro {
    opacity: 1;
  }
  
  .textpro {
    color: white;
    font-size: 10px!important;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
  }

